<template>
    <v-container style="width: 100%; " align="center">
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row >
                <v-col></v-col>
                <v-col class="text-center"><v-toolbar-title>{{usersText}}</v-toolbar-title></v-col>
                <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small tile style="display: inline;" text color="primary"
                                                 @click="openSearchFilter">{{toFilterText}}
                </v-btn></v-col>
            </v-row>


        </v-app-bar>
        <v-row style="margin-top: 54px">
            <v-col style="height: 65px">
                <v-text-field
                        v-model="searchByUsernameField"
                        :rules="[rules.min4Char]"
                        :label="searchLabel"
                        solo
                        dense
                        rounded
                ></v-text-field>
            </v-col>
        </v-row>
        <!--<v-text-field
                v-model="userNameSearchField"
                :rules="[rules.required, rules.min8Char, rules.samePassword]"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                :type="showPassword ? 'text' : 'password'"
                label="Contraseña"
                hint="Mínimo 8 caracteres"
                counter
                @click:append="showPassword = !showPassword"
                required
        ></v-text-field>-->
        <v-row dense>
            <v-col
                    v-for="user in users"
                    :key="user.uid"
                    cols="4"
            >
                <v-card class="text-center" style="padding: 5px"
                        @click="openUserProfile(user.uid)">
                    <v-img
                            class="white--text align-end"
                            :aspect-ratio="1"
                            :src="user.profileImage"
                    ></v-img>
                    <div>
                        <strong style="font-size: 12px">@{{user.username}}</strong>
                    </div>
                </v-card>
            </v-col>
            <v-col v-show="users.length == 0">
                <p>{{messageNoUsersFit}}</p>
            </v-col>

        </v-row>
        <v-row>
            <v-col>

                <v-container>
                    <v-btn v-show="!buttonIsDisabled" color="primary" @click="listUsers(false)" :disabled="buttonIsDisabled"
                           class="bottom-centered-buttons">
                        {{watchMoreButtonText}}
                    </v-btn>
                </v-container>
            </v-col>
        </v-row>


        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapActions, mapState, mapGetters, mapMutations} from 'vuex';

    export default {
        name: "SearchHome",
        data() {
            return {
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: null,
                searchLabel: Constants.TEXT_SEARCH,
                searchByUsernameField: '',
                messageNoUsersFit: '',
                watchMoreButtonText: Constants.TEXT_WATCH_MORE,
                myUID: this.$store.getters.getUserInfoUID,
                rules: {
                    min4Char: value => (value.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME || value.length == 0) || Constants.RULE_TEXT_MIN_VALUE_CHARACTERS_USERNAME_TO_START_SEARCHING,
                },
                usersText: Constants.TEXT_USERS,
                toFilterText: Constants.TEXT_TO_FILTER,
                closeText: Constants.TEXT_CLOSE

            }
        },
        methods: {
            ...mapActions([
                'listUsersSearchedPageFirebase',
                'isNextUsersSearchedPageEmptyFirebase',
                'searchUsersByUsernameFirebase',
                'listNearbyUsersSearchedPageFirebase',
                'isNextNearbyUsersSearchedPageEmptyFirebase'
            ]),

            languageRadioButtonsStateChange: function () {
                this.languageToLearnRadioButtonDisabled = !this.$store.getters.getSearchedUsersFilteringFilterByLanguageToLearn;
                this.knownLanguageRadioButtonDisabled = this.$store.getters.getSearchedUsersFilteringFilterByLanguageToLearn;
            },
            filterListUsers: function () {
                let self = this;
                self.buttonIsDisabled = true;

                if(self.filtering.maxDistance == 3){
                    let resultListUsersSearchedPageFirebase = this.listUsersSearchedPageFirebase({
                        orderBy: Constants.DATABASE_BIRTHDATE_FIELD,
                        first: true
                    });

                    resultListUsersSearchedPageFirebase
                        .then(() => {
                            self.isNextPageEmpty();
                            this.filterDialog = false;
                        })
                        .catch((error) => {
                            self.buttonIsDisabled = true;

                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }
                else{
                    let resultListNearbyUsersSearchedPageFirebase = this.listNearbyUsersSearchedPageFirebase({
                        first: true
                    });

                    resultListNearbyUsersSearchedPageFirebase
                        .then(() => {
                            self.isNextPageEmpty();
                            this.filterDialog = false;
                        })
                        .catch((error) => {
                            this.filterDialog = false;
                            console.log(error)
                        });
                }


            },
            searchUsers: function () {
                let self = this;
                self.buttonIsDisabled = true;

                if(self.filtering.maxDistance == 3){
                    let resultSearchUsersByUsernameFirebase = this.searchUsersByUsernameFirebase({
                        searchByUsernameField: self.searchByUsernameField,
                        orderBy: Constants.DATABASE_USERNAME_FIELD,
                    });


                    resultSearchUsersByUsernameFirebase
                        .then(() => {
                            //self.buttonIsDisabled = true;
                            self.isNextPageEmpty();
                        })
                        .catch((error) => {
                            //self.buttonIsDisabled = true;

                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }
                else{
                    let resultListNearbyUsersSearchedPageFirebase = this.listNearbyUsersSearchedPageFirebase({
                        first: false
                    });

                    resultListNearbyUsersSearchedPageFirebase
                        .then(() => {
                            this.filterDialog = false;
                            self.isNextPageEmpty();
                        })
                        .catch((error) => {
                            this.filterDialog = false;
                            console.log(error)
                        });
                }


            },
            listUsers: function (isFirst) {
                let self = this;
                self.buttonIsDisabled = true;

                if(self.filtering.maxDistance == 3){
                    let resultListUsersSearchedPageFirebase = this.listUsersSearchedPageFirebase({
                        orderBy: Constants.DATABASE_BIRTHDATE_FIELD,
                        first: isFirst
                    });

                    resultListUsersSearchedPageFirebase
                        .then(() => {
                            self.isNextPageEmpty();
                        })
                        .catch((error) => {
                            //self.buttonIsDisabled = true;

                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }
                else{
                    let resultListNearbyUsersSearchedPageFirebase = this.listNearbyUsersSearchedPageFirebase({
                        first: isFirst
                    });

                    resultListNearbyUsersSearchedPageFirebase
                        .then(() => {
                            self.isNextPageEmpty();
                            this.filterDialog = false;
                        })
                        .catch((error) => {
                            this.filterDialog = false;
                            console.log(error)
                        });
                }


            },
            isNextPageEmpty() {
                let self = this;

                if(self.filtering.maxDistance == 3){
                    let resultIsNextUsersSearchedPageEmptyFirebase = this.isNextUsersSearchedPageEmptyFirebase({
                        orderBy: Constants.DATABASE_BIRTHDATE_FIELD,
                    });

                    resultIsNextUsersSearchedPageEmptyFirebase
                        .then((isEmpty) => {
                            self.buttonIsDisabled = isEmpty;
                            this.messageNoUsersFit = Constants.TEXT_NO_FIT_USERS_TO_LIST;
                        })
                        .catch((error) => {
                            //self.buttonIsDisabled = true;

                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }
                else{
                    let resultIsNextNearbyUsersSearchedPageEmptyFirebase = this.isNextNearbyUsersSearchedPageEmptyFirebase({
                        orderBy: Constants.DATABASE_BIRTHDATE_FIELD,
                    });

                    resultIsNextNearbyUsersSearchedPageEmptyFirebase
                        .then((isEmpty) => {
                            self.buttonIsDisabled = isEmpty;
                            this.messageNoUsersFit = Constants.TEXT_NO_FIT_USERS_TO_LIST;
                        })
                        .catch((error) => {
                            //self.buttonIsDisabled = true;

                            self.snackbarText = error;
                            self.snackbar = true;
                        });
                }

            },
            openUserProfile(uid) {
                this.$router.push('search/user/' + uid);
            },
            openSearchFilter(){
                this.searchByUsernameField = '';
                this.$router.push('search/filter');
            },
            ...mapMutations([
                'resetSearchInfo'
            ]),
        },
        mounted() {
            if (this.users.length == 0) {
                this.listUsers(true);
            }
            else{
                this.isNextPageEmpty();
            }
        },
        computed: {
            ...mapState({
                lastVisible: state => state.searchInfo.lastVisible,
                filtering: state => state.searchInfo.filtering,
            }),
            ...mapGetters({
                users: 'getSearchUsers',
            }),
        },
        watch: {
            languagesRadioButtons: function () {
                if (this.languagesRadioButtons == "languageToLearnRadioButton") {
                    this.filtering.filterByLanguageToLearn = true;
                } else {
                    this.filtering.filterByLanguageToLearn = false;
                }
            },
            searchByUsernameField: function () {
                let self = this;

                if (self.searchByUsernameField.length >= Constants.RULE_MIN_VALUE_CHARACTERS_USERNAME) {
                    self.filtering.searchByUsername = true;
                    this.resetSearchInfo();
                    this.searchUsers();
                } else {
                    if (this.filtering.searchByUsername == true) {
                        this.filtering.searchByUsername = false

                        this.listUsers(true);

                    }

                }
            }
        },
    }
</script>

<style scoped>
    .bottom-centered-buttons {
        left: 50%;
        transform: translate(-50%, 0);
    }

    .filter-cards-titles {
        padding: 10px;
        padding-bottom: 5px;
        font-size: 18px;
    }

    .v-input__slot {
        margin: 0px;
    }

    p {
        word-wrap: inherit;
    }

    .app-bar-button {
        height: 100% !important;
        min-width: unset !important;
    }
</style>
